'use client';

import { useEffect } from 'react';
import LocomotiveScroll from 'locomotive-scroll';

export default function LocomotiveTestPage() {
  useEffect(() => {
    const scroll = new LocomotiveScroll({
      el: document.querySelector('[data-scroll-container]') as HTMLElement,
      smooth: true,
    });

    return () => scroll.destroy();
  }, []);

  return (
    <div data-scroll-container>
      <div data-scroll-section>
        <div className="h-screen bg-blue-500 flex items-center justify-center">
          <h1 className="text-4xl text-white" data-scroll data-scroll-speed="2">
            Section 1
          </h1>
        </div>
      </div>
      
      <div data-scroll-section>
        <div className="h-screen bg-red-500 flex items-center justify-center">
          <h1 className="text-4xl text-white" data-scroll data-scroll-speed="-1">
            Section 2
          </h1>
        </div>
      </div>
      
      <div data-scroll-section>
        <div className="h-screen bg-green-500 flex items-center justify-center">
          <h1 className="text-4xl text-white" data-scroll data-scroll-speed="3">
            Section 3
          </h1>
        </div>
      </div>
    </div>
  );
}